/* we are providing our own placeholder*/
._YrWkB::-moz-placeholder { 
	color: transparent;
}
._YrWkB:-ms-input-placeholder { 
	color: transparent;
}
._YrWkB::-ms-input-placeholder { 
	color: transparent;
}
._YrWkB::placeholder { 
	color: transparent;
}

/* Change the behaviour of the autofill color: */
._YrWkB:-webkit-autofill,
._YrWkB:-webkit-autofill:hover, 
._YrWkB:-webkit-autofill:focus, 
._YrWkB:-webkit-autofill:active  {

	/*autocomplete background color created from an inseted shadow:*/
	box-shadow: 0 0 0 30px transparent inset !important;

}

._YrWkB:-webkit-autofill{
	-webkit-text-fill-color: #000 !important; /*input text color*/
}

._YrWkB:-moz-placeholder-shown ~ ._2SLcE {
	font-size: 1.1rem;
	cursor: text;
	top: 28px;
}

._YrWkB:-ms-input-placeholder ~ ._2SLcE {
	font-size: 1.1rem;
	cursor: text;
	top: 28px;
}

._YrWkB:placeholder-shown ~ ._2SLcE {
	font-size: 1.1rem;
	cursor: text;
	top: 28px;
}

._2SLcE {
	position: absolute;
	top: -5px;
	left: 12px;
	/*display: block;*/
	transition: 0.2s;
	font-size: 0.9rem;
	background-color: transparent;

	/* disable the label interaction*/
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	pointer-events: none;

	/*given dinamically:*/
	/*color: #333;*/
}
  


._YrWkB:focus ~ ._2SLcE {
	
	position: absolute;
	top: -5px;
	left: 12px;
	/*display: block;*/
	transition: 0.2s;
	font-size: 1rem;
	font-weight: 500;
	
	
	/*given dinamically:*/
	/*color: #11998e;*/
}

/* reset input */
._YrWkB:required, ._YrWkB:invalid {
	box-shadow: none;
}

